import React from "react"
import styles from "./about-css-modules.module.css"

import Container from "../components/container"

const User = props =>
  <div className={styles.user}>
    <img src={props.avatar} className={styles.avatar} alt="" />
    <div className={styles.description}>
      <h2 className={styles.username}>
        {props.username}
      </h2>
      <p className={styles.excerpt}>
        {props.excerpt}
      </p>
    </div>
  </div>

export default () =>
  <Container>
    <h1>发现·头条</h1>
    <p>世界如此精彩~</p>
    <User
      username="杭州餐厅的小众点评"
      avatar="https://cdn.nlark.com/yuque/0/2019/png/84141/1550635801697-a88e2559-18d8-40e7-ba65-9dcd495fd22c.png?x-oss-process=image/resize,m_fill,h_96,w_96"
      excerpt="今天跟大家分享我家附近的一家日料店。各种机缘巧合，这家店刚开不久，我就光顾过，应算是六月的早期食客了。"
    />

    <User
      username="新知周报-18"
      avatar="https://cdn.nlark.com/yuque/0/2019/png/84141/1550105678728-04427630-7133-4993-b48d-416ce698f337.png?x-oss-process=image/resize,m_fill,h_96,w_96"
      excerpt="记录过去一周我看到且值得分享的内容，创新/技术/科学。"
    />

  </Container>